@import "global";
@import "header";
@import "text";
@import "menu";

#scheme {
  // background-image: url("assets/scheme/axes.svg");

  // padding: $header-height 0 0 0;

  position: absolute;
  top: $header-height;
  left: $margin-width;
  right: $margin-width;
  bottom: $margin-width;

  overflow: hidden;

  #axes {
    height: 100%;
    width: 100%;
    // object-fit: cover;
    // background-color: red;

    object-fit: cover;
    // ricordati che Ã¨ temporaneo poi dovrai separare le icone e mettere cover
  }
}



#scheme_name_container, #scheme_img_container {
    position: absolute;
    width: 100%;
    height: 100%;
    padding: 2rem;
}



.axes-index {
  position: absolute;
  text-align: center;

  img {
    width: 1.5rem;
  }
}

#axes-index-1 {
  height: 1.5rem; width: 100%; top: 0; left: 0;
img {transform: translate(0, -80%)}
}
#axes-index-2 {height: 1.5rem; width: 100%; bottom:0; left:0;
img {transform: translate(0, -80%)}
}
#axes-index-3 {width: 1.5rem; height: 100%; top: 0; left: 0;
img {position: absolute; left: 0; top: 50%; transform: translate(0, -50%)}
}
#axes-index-4 {width: 1.5rem; height: 100%; top: 0; right: 0;
img {position: absolute; right: 0; top: 50%; transform: translate(0, -50%)}
}

#laico {
  transform:rotate(-90deg)translate(20%, -70%);
  position: absolute;
  top: 50%;
  left: 0;
}
#religioso {
  transform:rotate(90deg)translate(-10%, -155%);
  position: absolute;
  top: 50%;
  right: 0;
}


.axes-index-name {
-webkit-animation: disappear ease 2s ;
animation: disappear ease 2s ;
animation-delay: 3s;
animation-fill-mode: forwards;
}
@-webkit-keyframes disappear {
0% { opacity: 1; }
100% { opacity: 0; }
}
@keyframes disapper {
0% { opacity: 1; }
100% { opacity: 0; }
}

.axes-index-icon {
opacity: 0;
-webkit-animation: appear ease 2s ;
animation: appear ease 2s ;
animation-delay: 3s;
animation-fill-mode: forwards;
}

@-webkit-keyframes appear {
0% { opacity: 0; }
100% { opacity: 1; }
}
@keyframes appear {
0% { opacity: 0; }
100% { opacity: 1; }
}


.rito, .rito2, #progetto {
  background-color: $grey;
  cursor: pointer;
